.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: #282c34;

}


.Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0 10px;
}

img {
  height: 400px;
  width: 400px;
  object-fit: cover;
}

button {
  background-color: crimson;
  color: #fff;
  position: absolute;
  top: 96%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px 40px;
  border: none;
  border-radius: 25px;

  cursor: pointer;
  z-index: 3; /* 버튼을 오버레이와 구별 */
}


.overlay-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
  z-index: 2;
  pointer-events: none; /* Prevent the overlay from capturing click events */
}

.overlay {
  width: 80%; /* 적절한 크기로 설정 */
  height: auto; /* 비율 유지 */
  object-fit: cover; /* 화면 비율을 유지하며 채우기 */
}


.hidden-canvas {
  display: none;
}

.captured-image-container {
  margin-top: 20px;
  text-align: center;
}

.captured-image {
  max-width: 100%;
  height: auto;
  border: 2px solid #ccc;
  margin-top: 10px;
}